export function isObject(value) {
    return typeof value === 'object' && value !== null;
}
export function isSingleTuple(array) {
    return array.length === 1;
}
export function objectValues(o) {
    return Object.keys(o).map(prop => o[prop]);
}
export function objectEntries(o) {
    return Object.keys(o).map(prop => [prop, o[prop]]);
}
export function mergeSet(set1, ...setNArray) {
    const newSet = new Set(set1);
    for (const setN of setNArray) {
        if (setN) {
            for (const value of setN) {
                newSet.add(value);
            }
        }
    }
    return newSet;
}
